angular.module('VVBank').directive('indentification', function() {
    return {

        // limit usage to argument only
        restrict: 'A',

        // require NgModelController, i.e. require a controller of ngModel directive
        require: 'ngModel',

        // create linking function and pass in our NgModelController as a 4th argument
        link: function(scope, element, attr, ctrl) {
            ctrl.$parsers.push(function(ngModelValue) {
                if (getIdCardInfo(ngModelValue).isTrue) {
                    ctrl.$setValidity('indentification', true)
                } else {
                    ctrl.$setValidity('indentification', false)
                }
                return ngModelValue;
            })
        }
    };
});
(function(window) {
    /**
     * 
     *
     * @param cardNo
     *            {String} 
     * @returns info {Object} .
     *
     */
    function getIdCardInfo(cardNo) {
        var info = {
            isTrue: false, //  false
            year: null, // null
            month: null, // null
            day: null, // null
            isMale: false, // false
            isFemale: false // false
        };

        if (!cardNo || (15 != cardNo.length && 18 != cardNo.length)) {
            info.isTrue = false;
            return info;
        }

        if (15 == cardNo.length) {
            var year = cardNo.substring(6, 8);
            var month = cardNo.substring(8, 10);
            var day = cardNo.substring(10, 12);
            var p = cardNo.substring(14, 15); // 
            var birthday = new Date(year, parseFloat(month) - 1, parseFloat(day));
            // getYear()
            if (birthday.getYear() != parseFloat(year) || birthday.getMonth() != parseFloat(month) - 1 || birthday.getDate() != parseFloat(day)) {
                info.isTrue = false;
            } else {
                info.isTrue = true;
                info.year = birthday.getFullYear();
                info.month = birthday.getMonth() + 1;
                info.day = birthday.getDate();
                if (p % 2 == 0) {
                    info.isFemale = true;
                    info.isMale = false;
                } else {
                    info.isFemale = false;
                    info.isMale = true;
                }
            }
            return info;
        }

        if (18 == cardNo.length) {
            var year = cardNo.substring(6, 10);
            var month = cardNo.substring(10, 12);
            var day = cardNo.substring(12, 14);
            var p = cardNo.substring(14, 17);
            var birthday = new Date(year, parseFloat(month) - 1, parseFloat(day));
            // getFullYear()
            if (birthday.getFullYear() != parseFloat(year) || birthday.getMonth() != parseFloat(month) - 1 || birthday.getDate() != parseFloat(day)) {
                info.isTrue = false;
                return info;
            }

            var Wi = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2, 1]; // 
            var Y = [1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2]; // .10X

            // 
            var sum = 0; // 
            var _cardNo = cardNo.split("");

            if (_cardNo[17].toLowerCase() == 'x') {
                _cardNo[17] = 10; // x10
            }
            for (var i = 0; i < 17; i++) {
                sum += Wi[i] * _cardNo[i]; // 
            }
            var i = sum % 11; // 

            if (_cardNo[17] != Y[i]) {
                return info.isTrue = false;
            }

            info.isTrue = true;
            info.year = birthday.getFullYear();
            info.month = birthday.getMonth() + 1;
            info.day = birthday.getDate();

            if (p % 2 == 0) {
                info.isFemale = true;
                info.isMale = false;
            } else {
                info.isFemale = false;
                info.isMale = true;
            }
            return info;
        }
        return info;
    }
    window.indentification = getIdCardInfo;
})(window);
