var messagesController = function($scope, errorServices, myServices, toastServices,parserServices, config) {
    $scope.messages = [];
    $scope.page = {
        number:1,
        page_size:1,
        message:""
    }
    $scope.loadMore = function() {
        if ($scope.no_more) {
            return;
        }
        toastServices.show();
        $scope.page.message ="...";
        myServices.message.query($scope.page.number).then(function(data) {
            toastServices.hide();
            $scope.page.message ="";
            if (data.respcode == config.request.SUCCESS) {
                $scope.messages = $scope.messages.concat(parserServices.parseMessages(data.result));
            } else {
                errorServices.autoHide("");
            }
            if (data.result.length == 0) {
                $scope.no_more = true;
                $scope.page.message = "...";
            }
            $scope.page.number++;
        })
    
    }
    $scope.loadMore();
}
